<template>
    <div>
      <div class="titleEducation">
        <h2>, </h2>
      </div>
      <v-form v-model="valid" ref="form" class="formInput">
        <div>
          <v-select
            :items="itemsType"
            :loading="loading"
            v-model="valueProfile.type"
            :rules="typeRules"
            label=""
          ></v-select>
          <v-select
            :items="itemsLevel"
            :loading="loading"
            v-model="valueProfile.level"
            :rules="typeRules"
            label=""
          ></v-select>
          <v-text-field
            v-model="valueProfile.institute"
            :loading="loading"
            :rules="typeRules"
            label=" "
            required
          ></v-text-field>
          <v-text-field
            v-model="valueProfile.specialty"
            :loading="loading"
            label=""
            required
          ></v-text-field>
        </div>
      </v-form>
    </div>
</template>

<script>
export default {
  name: 'education',
  data () {
    return {
      valid: false,
      itemsType: ['', '', '', ''],
      itemsLevel: ['', '-', ' ', '', ' '],
      typeRules: [
        v => !!v || ' '
      ]
    }
  },
  computed: {
    valueProfile () {
      return this.$store.getters['dataStore/getDataInputProfile'].education
    },
    loading () {
      return this.$store.getters['dataStore/getLoadingInput']
    }
  }
}
</script>

<style scoped>
  .titleEducation{
    text-align: left;
  }
  .titleEducation h2{
    font-weight: 400;
  }
</style>



